.slider {
	position: relative;
	&__prev, &__next {
		height: 80px;
		width: 80px;
		background-color: $yellow;
		border-radius: 50%;
		transform: translateY(-50%);
		top: 50%;
		position: absolute;
		z-index: 10;
		background-image: url('../img/icon_arrow.svg');
		background-position: 50% 50%;
		background-repeat: no-repeat;
		background-size: 14px;
		top: 50%;
		cursor: pointer;
		@include r(1279) {
			top: 41%; } }
	&__next {
		right: 0;
		@include r(1279) {
			right: -95px; } }
	&__prev {
		transform: translateY(-50%) rotate(-180deg);
		@include r(1279) {
			left: -95px; } }
	&__item {
		background: $yellow;
		height: 12px;
		width: 12px;
		display: block;
		border-radius: 50%;
		margin-right: 20px; }
	&__dots {
		display: inline-block;
		position: absolute;
		text-align: center;
		left: 0;
		right: 0;
		bottom: 0;
		@include r(1279) {
			left: 0;
			text-align: left; }
		&  .slick-active .slider__item {
			background: $crimson; }
		li {
			display: inline-block;
			cursor: pointer; } } }

.client {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	@include clr;
	@include r(1279) {
		display: block; }
	// &__col
	// 	max-width: 405px
	// 	width: 100%
	&__col {
		width: 50%;
		@include r(1279) {
			width: 100%; } }
	&__block {
		@include r(1279) {
			display: flex;
			align-items: center; } }
	&__img {
		width: 425px;
		height: 425px;
		background-size: cover;
		margin-bottom: 15px;
		margin-left: 30px;
		@include r(1279) {
			width: 155px;
			height: 155px;
			margin-left: 0;
			margin-right: 23px; }
		img {
			width: 100%;
			display: block; } }
	&__title {
		margin-bottom: 55px;
		@include clr; }
	&__title-wrap {
		float: right;
		width: 50%;
		@include r(1279) {
			width: 100%; } }
	&__sub-title {
		margin-bottom: 55px;
		max-width: 405px;
		@include r(1279) {
			max-width: 100%; } }
	&__descr {
		margin-bottom: 87px;
		max-width: 405px;
		@include r(1279) {
			max-width: 100%;
			margin-bottom: 60px; }
		p {
			line-height: 30px;
			letter-spacing: 0.7px; } }
	&__link {
		float: right;
		display: block; } }
