.application {
	padding: 56px 0 102px;
	position: relative;
	@include r(1023) {
		padding-bottom: 0; }
	@include r(767) {
		display: none; }
	&__bg {
		top: -144px;
		width: 771px;
		position: absolute;
		left: 31%;
		@include r(1023) {
			position: relative;
			left: -168px;
			margin-bottom: 23px; }
		img {
			display: block;
			max-width: 100%; } }
	&__wrap {
		display: flex;
		align-items: flex-end;
		@include r(1023) {
			display: block; } }
	&__info {
		max-width: 410px;
		margin-right: 25px;
		@include r(1023) {
			max-width: 100%;
			margin-bottom: 70px; } }
	&__row {
		display: flex; }
	&__title {
		margin-bottom: 57px; }
	&__descr {
		max-width: 322px;
		margin-bottom: 45px;
		@include r(1023) {
			max-width: 100%; } }
	&__subtitle {
		margin-bottom: 32px;
		font-size: 18px;
		letter-spacing: 0.9px; }
	&__list {
		margin-bottom: 50px;
		max-width: 383px;
		@include r(1023) {
			max-width: 100%; } }
	&__icon {
		position: relative;
		&:not(:last-child) {
			margin-right: 16px; } } }

.list-appl {
	li {
		margin-bottom: 14px;
		position: relative;
		padding-left: 25px;
		line-height: 24px;
		&:before {
			content: '';
			position: absolute;
			height: 6px;
			width: 6px;
			background: $violet;
			border-radius: 50%;
			top: 10px;
			left: 0; } } }

.icon-app {
	display: flex;
	background-color: $grey;
	border-radius: 40px;
	width: 280px;
	justify-content: center;
	height: 80px;
	align-items: center;
	text-decoration: none;
	transition: background-color 0.5s;
	&:hover {
		background: $white;
		border: 1px solid #aeb7bc; }
	&:hover {
		.icon-app__text-small {
			color: $grey; } }
	&:hover {
		.icon-app__text-big {
			color: $grey; } }
	&:hover {
		.icon-apple {
			font-size: 40px;
			fill: #000; } }
	&__img {
		margin-right: 19px;
		width: 40px;
		.icon-apple {
			font-size: 40px;
			transition: fill 0.5s;
			fill: #fff; } }
	&__text-small {
		font-size: 10px;
		font-weight: 700;
		line-height: 30px;
		text-transform: uppercase;
		letter-spacing: 0.5px;
		color: #fff;
		transition: color 0.5s; }
	&__text-big {
		color: #fff;
		font-family: $Panton;
		font-size: 24px;
		font-weight: 400;
		letter-spacing: 1.2px;
		transition: color 0.5s; } }

