.work-program {
	position: relative;
	// padding-bottom: 67px
	padding-top: 145px;
	@include r(767) {
		padding-top: 100px; }
	&__bg {
		position: absolute;
		left: 50%;
		top: 0;
		transform: translateX(-50%);
		margin-top: 316px;
		// width: 100%
		img {
			@include r(1279) {
				height: 100%; } }
		@include r(1600) {
			width: 1600px; }
		@include r(1279) {
			height: 2446px;
			margin-top: 281px;
			margin-left: 31px; }
		@include r(767) {
			height: 1906px;
			margin-top: 165px;
			margin-left: 222px; }
		// +md
		// 	margin-top: -560px
		// img
		// 	width: 100%
 }		// 	display: block
	&__in {
		// margin-bottom: 256px
		@include r(1279) {
			margin-bottom: 126px; }
		@include r(767) {
			margin-bottom: 115px; } }
	&__bg-j {
		position: absolute;
		left: 50%;
		top: 0;
		transform: translateX(-50%);
		width: 414px;
		// margin-top: -882px
		margin-left: 118px;
		@include r(1279) {
			margin-top: 0;
			margin-left: -253px; }
		@include r(767) {
			width: 267px;
			margin-top: 0;
			margin-left: -167px; }
		img {
			display: block;
			width: 100%; } }
	&__bg-j-sm {
		position: absolute;
		left: 50%;
		top: 0;
		transform: translateX(-50%);
		width: 230px;
		margin-top: 0;
		margin-left: 460px;
		@include r(1279) {
			margin-top: 0;
			margin-left: 95px; }
		@include r(767) {
			margin-top: 0;
			margin-left: 54px;
			width: 143px; }
		img {
			display: block;
			width: 100%; } }
	&__title {
		max-width: 500px;
		position: relative;
		margin-bottom: 10px;
		margin-bottom: 227px;
		z-index: 1;
		@include r(767) {
			margin-bottom: 115px; } }
	&__subtitle {
		margin-bottom: 184px;
		max-width: 500px;
		position: relative;
		z-index: 1;
		display: none;
		@include r(1279) {
			margin-bottom: 126px; } }
	&__wrap {
		// max-width: 887px
		margin-left: 82px;
		position: relative;
		@include md {
			margin-left: 0; } }
	&__col {
		&_mt {
			padding-top: 160px; } } }

.list-work {
	@include clr;
	&__item {
		max-width: 354px;
		position: relative;
		margin-bottom: 141px;
		@include r(1279) {
			max-width: 100%;
			margin-bottom: 82px; }
		@include r(767) {
			margin-bottom: 71px;
			min-height: 145px;
			padding-top: 60px; }
		&:not(:first-child) {
			@include r(1279) {
				margin-bottom: 47px; }
			@include r(767) {
				margin-bottom: 27px; } }
		&:nth-child(even) {
			float: right;
			// max-width: 360px
			@include r(1279) {
				text-align: right; }
			@include r(767) {
				float: none;
				text-align: left; }
			& .list-work__bg {
				right: 255px;
				@include r(1279) {
					right: -66px; }
				@include r(767) {
 } } }					// margin-right: 26px
		&:nth-child(odd) {
			float: left;
			@include r(767) {
				float: none; }
			& .list-work__bg {
				left: -66px;
				// +r(1279)
				// 	margin-left: 58%
				@include r(767) {
 } } }					// margin-right: 36px
		&_mt {
			margin-top: 165px;
			@include r(1279) {
				margin-top: 0; } } }
	&__bg {
		position: absolute;
		line-height: 145px;
		top: 0;
		margin-top: -48px;
		// left: -66px
		display: flex;
		align-items: flex-end;
		@include r(767) {
			margin-top: 0; }
		img {
			width: 100%;
			display: block; } }
	&__title {
		margin-bottom: 31px;
		position: relative;
		// max-width: 351px
		letter-spacing: 0.3px;
		line-height: 47px;
		z-index: 1;
		@include r(767) {
			margin-bottom: 41px;
			line-height: 30px; } }
	&__descr {
		font-size: 18px;
		line-height: 30px;
		position: relative;
		letter-spacing: 0.9px;
		margin-bottom: 51px;
		// max-width: 350px
		@include r(1279) {
			margin-bottom: 28px; }
		@include r(767) {
			display: none; } }
	&__number {
		font-size: 234px;
		color: #fff;
		margin-left: 11px;
		font-weight: 900;
		letter-spacing: 2.34px;
		font-family: $Panton;
		display: block;
		@include r(1279) {
			font-size: 191.8px; } }
	&__icon {
		flex: none;
		@include r(1279) {
			max-width: 150px; } }
	&__button {
		position: relative;
		z-index: 1; } }

